import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {HttpClientModule, HTTP_INTERCEPTORS} from '@angular/common/http';
import { AngularFontAwesomeModule } from 'angular-font-awesome';

// https://github.com/aitboudad/ngx-loading-bar
import { LoadingBarRouterModule } from '@ngx-loading-bar/router';

import { AppRoutingModule } from './app-routing.module';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MenuComponent } from './menu/menu.component';
import { SearchComponent } from './search/search.component';
import { HomeSearchComponent } from './home-search/home-search.component'; 
import { HeaderComponent } from './inc/header/header.component';
import { SpecialitiessectionComponent } from './inc/specialitiessection/specialitiessection.component';
import { HowitworkssectionComponent } from './inc/howitworkssection/howitworkssection.component';
import { ProvidesSectionComponent } from './inc/provides-section/provides-section.component';
import { HealthtipsSectionComponent } from './inc/healthtips-section/healthtips-section.component';
import { HomeComponent as PracticeHomeComponent } from './practice/home/home.component';

// core modules
import { MaterialModule } from './core/material.module';

// Import your library
import { OwlModule } from 'ngx-owl-carousel';


// Import helpers
import { JwtInterceptor, ErrorInterceptor } from './_helpers';
import { FooterComponent } from './inc/footer/footer.component';
import { PartnersComponent } from './inc/partners/partners.component';
import { TestimonialComponent } from './inc/testimonial/testimonial.component';
import { NewssectionComponent } from './inc/newssection/newssection.component';
import { PopupmodalLoginComponent } from './inc/popupmodal-login/popupmodal-login.component';
import { SearchPageComponent } from './search-page/search-page.component';
import { InnheaderComponent } from './inc/innheader/innheader.component';
import { FilterSectionComponent } from './inc/filter-section/filter-section.component';
import { SearchResultItemComponent } from './inc/search-result-item/search-result-item.component';
import { SignupPageComponent } from './signup-page/signup-page.component';
import { InnheadercommonComponent } from './inc/innheadercommon/innheadercommon.component';
import { SignformsectionComponent } from './inc/signformsection/signformsection.component';
import { ProfessionalComponent } from './professional/professional.component';
import { ProfessionalformsectionComponent } from './inc/professionalformsection/professionalformsection.component';



@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    LoginComponent,
    MenuComponent,
    HomeSearchComponent,
    SearchComponent,
    HeaderComponent,
    SpecialitiessectionComponent,
    HowitworkssectionComponent,
    ProvidesSectionComponent,
    HealthtipsSectionComponent,
    FooterComponent,
    PartnersComponent,
    TestimonialComponent,
    NewssectionComponent,
    PopupmodalLoginComponent,
    SearchPageComponent,
    InnheaderComponent,
    FilterSectionComponent,
    SearchResultItemComponent,
    SignupPageComponent,
    InnheadercommonComponent,
    SignformsectionComponent,
    ProfessionalComponent,
    ProfessionalformsectionComponent,
    PracticeHomeComponent
  ],
  imports: [
    HttpClientModule,
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MaterialModule,
    FormsModule,  
    ReactiveFormsModule,
    LoadingBarRouterModule, 
    OwlModule,
    AngularFontAwesomeModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
