import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { debounceTime } from 'rxjs/internal/operators/debounceTime';
import { map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private httpService: HttpClient) { }

  customer_login(data: any) {
    var results = this.httpService.post(environment.apiUrl + 'auth/patient/login', data, {
      observe: 'response',
      responseType: 'json'
    });

    return <any>results;
  }

  practice_login(username: string, password: string) {
    var user_data = "";
    localStorage.setItem('user_data', user_data);
    return [username, password];
  }

  customer_signup(data): any {
    var results = this.httpService.post(environment.apiUrl + 'patient', data, {
      observe: 'response',
      responseType: 'json'
    });

    return results;
  }

  practice_signup(data: any[]) {
    var user_data = "";
    localStorage.setItem('user_data', user_data);
    return data;
  }

  logout() {
    // remove user from local storage to log user out
    localStorage.removeItem('user_data');
  }

  customer_account_verify(data): any {
    var token = JSON.parse(this.getSession('unverify_user'));
    data.token = token;
    var results = this.httpService.post(environment.apiUrl + 'patient/account_verify', data, {
      observe: 'response',
      responseType: 'json'
    });
    return results;
  }

  getCurrentUser(): any {
    return localStorage.getItem('user_data');
  }

  setUser(user_data): any {
    localStorage.setItem('user_data', JSON.stringify(user_data));
  }

  setSession(key, data) {
    sessionStorage.setItem(key, JSON.stringify(data));
  }

  getSession(key) {
    return sessionStorage.getItem(key);
  }

  removeSession(key) {
    sessionStorage.removeItem(key);
  }

}
